<template>
<div class="container">
  <h1>Arabic to Roman numeral converter</h1>
<input v-model="input" placeholder="blah">
<Button text="convert" @convert="clickFun()"></Button>
<p>Result is {{output}}</p>
</div>
</template>

<script>
import { toRoman } from "./business.js";
import  Button  from "./components/Button";
export default {
  name: 'App',
  components: {
    Button
  },
  data(){
    return{
      input:0,
      output:""
    }
  },
  methods:{
    clickFun(){
      this.output = toRoman(this.input);
    }
  }
}
</script>

